<template>
  <div id="app">
    <real-header></real-header>
    <qna-header></qna-header>
    <router-view></router-view>  
  </div>
</template>

<script>
import Vue from 'vue';
import VueRouter from 'vue-router';
import realHeader from './components/realHeader.vue'
import qnaHeader from './components/qnaHeader.vue';
import qnaList from './components/qnaList.vue';
import qnaDetail from './components/qnaDetail.vue';
import qnaAdd from './components/qnaAdd.vue';
import qnaUpdate from './components/qnaUpdate.vue';
Vue.use(VueRouter);


const router = new VueRouter({
  mode : 'history',
  routes : [
  {path:'/',component:qnaList},
  {path:'/list',component:qnaList},
  {path:'/update',component:qnaUpdate},
  {path:'/detail/:qnaNo',component:qnaDetail},
  {path:'/add',component:qnaAdd}
  ]
});

export default {
  name: 'app',
  components: {
    qnaHeader,realHeader
  },
  router,
  data() {
    return {
      qnalist: [],
      qna : {}
    }
  },
}
</script>

