        }
    },
    methods : {
        addTodo : function() {
            axios
            .get('/api/qna/id')
            .then((response)=>{
                this.qnaUserid = response.data;
                this.$store.dispatch('addQnA', {qnaTitle : this.qnaTitle, 
                                                qnaContent: this.qnaContent, 
                                                qnaUserid: this.qnaUserid});
                this.qnaTitle = "";
                this.qnaContent = "";
                this.qnaUserid = "";
                this.$router.push('/list');
            })
            .catch(exp => alert("Ï∂îÍ∞ÄÏ≤òÎ¶¨Ïóê Ïã§Ìå®ÌïòÏòÄÏäµÎãàÎã§." + exp));
            
            

            
            //this.$store.dispatch('allQnA');
            //location.href ="/happyhouse/";
        }
    }
}
</script>

